@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&family=Titillium+Web:wght@300&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;

}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  height: 130vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: white;
}

.app::before{
  content: "";
  background: url("./assets/weather-bg.jpg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container {
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
/* .app .search-box {
  display: flex;
  justify-content: center;
} */
.app .search {
  text-align: center;
  padding: 1rem;
  margin-top: 0.5rem;
}
.app input {
  width: 25%;
  padding: 0.7rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255,08);
  background: rgba(255,255,255,0.1);
  color: #f8f8f8;
}
::placeholder  {
  color: #f8f8f8;
  opacity: 65%;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}
.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0 ;
  transform: rotate(271deg);
}
.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.2);
}

.bold {
  font-weight: 700;
}